<template>
  <div>
    <HomeView v-if="appState===1"
              @startMatch="startMatch"/>
    <MatchView v-else
               ref="matchView"
               @onSettingsClick="showSettings"
               :match-duration="matchDuration"
               :first-half="firstHalf"
               :second-half="secondHalf"
    />
  </div>
</template>

<script>
import MatchView from './views/MatchView.vue'
import HomeView from '@/views/HomeView';

export default {
  name: 'App',
  components: {
    HomeView,
    MatchView
  },
  data() {
    return {
      appState: 1,
      matchDuration: 10,
      firstHalf: 15,
      secondHalf: 10,
    }
  },
  methods: {
    startMatch(matchDuration, firstHalf, secondHalf) {
      this.matchDuration = parseInt(matchDuration);
      this.firstHalf = parseInt(firstHalf);
      this.secondHalf = parseInt(secondHalf);
      this.appState = 2;
    },
    showSettings() {
      this.appState = 1;
    }
  }
}
</script>

