<template>
  <canvas id="myCanvas" width="100" height="100"/>
</template>

<script>
export default {
  name: 'CanvasDisplay',
  props: {
    secondsForShot: Number,
    msSecondsToShoot: Number,
    shootTime: Number,
  },
  watch: {
    secondsForShot() {
      this.update();
    }
  },
  data() {
    return {
      ctx: null,
      cW: 0,
      cH: 0,
      size: 0
    }
  },
  methods: {
    init() {
      const canvas = document.getElementById('myCanvas');
      this.ctx = canvas.getContext('2d');

      this.ctx.canvas.width = canvas.parentNode.offsetWidth;
      this.ctx.canvas.height = canvas.parentNode.offsetHeight;

      canvas.width = canvas.parentNode.offsetWidth;
      canvas.height = canvas.parentNode.offsetHeight;
      canvas.style.width = canvas.parentNode.offsetWidth + 'px';
      canvas.style.height = canvas.parentNode.offsetHeight + 'px';

      this.cW = canvas.parentNode.offsetWidth;
      this.cH = canvas.parentNode.offsetHeight;

      this.size = (this.cW < this.cH) ? this.cW : this.cH;

      this.update();

    },
    update() {
      this.ctx.clearRect(0, 0, this.ctx.canvas.width, this.ctx.canvas.height);

      this.drawShotTimeLeft();

      const gap = 0.005;
      const segments = this.shootTime;
      this.drawCircle(segments, Math.ceil(this.secondsForShot), gap, this.size / 2 - 60, '#ffffff', 30);
      this.drawCircle(segments, segments, gap, this.size / 2 - 40, '#f5a700', 2);

      this.drawShotMs();
    },
    drawShotTimeLeft() {
      this.ctx.font = (this.size / 2 - 55) + 'px Audiowide';
      this.ctx.textAlign = 'center';
      this.ctx.textBaseline = 'middle';
      this.ctx.fillStyle = 'rgba(255,255,255,1)';
      this.ctx.fillText(Math.ceil(this.secondsForShot).toString(), this.cW / 2, this.cH / 2);
    },

    drawShotMs() {
      this.ctx.beginPath();
      this.ctx.lineWidth = 10;
      this.ctx.strokeStyle = '#5bab00';

      const start = 0;
      const end = (this.msSecondsToShoot / 100) * (this.cW * 0.3);
      const y = this.cH / 2 + ((this.size / 2 - 40) / 2);
      const middle = this.cW / 2 - ((end - start) / 2);
      this.ctx.moveTo(start + middle, y);
      this.ctx.lineTo(end + middle, y);
      this.ctx.stroke();
    },
    drawCircle(segments, offset, gap, radius, color, width) {

      this.ctx.strokeStyle = color;
      this.ctx.lineWidth = width;
      let from, to = 0;
      const step = ((1 / segments) * 2 * Math.PI);
      for (let i = 0; i < offset; i++) {
        from = (i * step - 0.5 * Math.PI);
        to = ((i + 1) * step - 0.5 * Math.PI);

        this.ctx.beginPath();
        this.ctx.arc(this.cW / 2, this.cH / 2, radius, from + gap, to - gap, false);
        this.ctx.stroke();
      }
    }
  },
  mounted() {
    this.init();
    // setInterval(this.update, 100)
  }
}
</script>

