<template>
  <div v-if="mounted" class="layout">
    <div class="menu">
      <button @click="onSettingsClick()" class="sbtn">Abort / Settings</button>
    </div>
    <div class="canvas">
      <canvas-display
          :seconds-for-shot="timeToShoot"
          :msSecondsToShoot="mstimeToShoot"
          :shoot-time="shootTime"
      />
    </div>

    <div class="match-clock">{{ matchTimeRemaining }}</div>

    <div>
      <button @click="onButtonClick" class="sbtn">Start / Stop</button>
    </div>
  </div>
</template>

<script>
import CanvasDisplay from '@/components/CanvasDisplay';

export default {
  name: 'MatchView',
  emits: ['onSettingsClick'],
  components: {CanvasDisplay},
  props: {
    matchDuration: Number,
    firstHalf: Number,
    secondHalf: Number
  },
  computed: {
    shootTime() {
      if (this.switchedTimer) {
        return this.secondHalf;
      }

      return this.firstHalf;
    }
  },
  data() {
    return {
      matchStarted: false,
      switchedTimer: false,
      now: new Date(),
      started: false,
      startTime: null,
      matchTime: null,
      matchTimeRemaining: null,
      timeToShoot: 0,
      mstimeToShoot: 100,
      timerRunning: false,
      mounted: false,
      shotInterval: null,
      matchIntervall: null,
      timer5played: false,
      timer4played: false,
      timer3played: false,
      timer2played: false,
      timer1played: false,
      secondHalfPlayed: false,
      twoMinutesPlayed: false,
      oneMinutePlayed: false,
      tensecondsPlayed: false,
      matchOverPlayed: false,
      sounds: {
        timer5: null,
        timer4: null,
        timer3: null,
        timer2: null,
        timer1: null,
        foul: null,
        beep: null,
        secondHalf: null,
        twoMinutes: null,
        oneMinute: null,
        tenseconds: null,
        matchOver: null,
      }
    }
  },
  methods: {
    onSettingsClick() {
      this.$emit('onSettingsClick');
    },
    start() {
      this.timeToShoot = this.shootTime;
      this.matchTime = new Date();

      this.matchTime.setMinutes(this.matchTime.getMinutes() + this.matchDuration);
      this.startTime = new Date();
      this.resetTimerSounds();
      this.shotInterval = setInterval(this.handleTimer, 10)
      this.matchIntervall = setInterval(this.handleMatchTimer, 10)
      this.matchStarted = true;
      this.switchedTimer = false;
    },
    handleMatchTimer() {
      this.now = new Date();
      const diff = this.matchTime.getTime() - this.now.getTime();

      if (diff / 1000 <= 120 && this.matchDuration > 2) {
        if (!this.twoMinutesPlayed) {
          this.sounds.twoMinutes.play();
          this.twoMinutesPlayed = true;
        }
      }

      if (diff / 1000 <= 60 && this.matchDuration > 1) {
        if (!this.oneMinutePlayed) {
          this.sounds.oneMinute.play();
          this.oneMinutePlayed = true;
        }
      }

      if (diff / 1000 <= 10) {
        if (!this.tensecondsPlayed) {
          this.sounds.tenseconds.play();
          this.tensecondsPlayed = true;
        }
      }

      if (diff <= 0) {
        this.matchTimeRemaining = '00:00';
        this.matchStarted = false;
        this.timerRunning = false;
        if (!this.matchOverPlayed) {
          this.sounds.matchOver.play();
          this.matchOverPlayed = true;
        }
        return;
      }

      const minutes = Math.floor(diff / 1000 / 60);
      let seconds = Math.floor((diff / 1000) % 60);
      if (seconds < 10) {
        seconds = '0' + seconds;
      }

      //const msseconds = Math.floor(((diff) % 1000) / 100);
      this.matchTimeRemaining = minutes + ':' + seconds;

      const timeStampNow = this.now.getTime();
      const timeStampHalfTime = this.matchTime.getTime() - (this.matchDuration / 2 * 60000);
      const toGo = timeStampHalfTime - timeStampNow;

      if (toGo < 0 && !this.sounds.secondHalfPlayed) {
        this.sounds.secondHalf.play();
        this.sounds.secondHalfPlayed = true;
      }

      if (toGo < 0 && !this.timerRunning && !this.switchedTimer) {
        this.switchedTimer = true;
      }
    },
    resetTimerSounds() {
      this.timer1played = false;
      this.timer2played = false;
      this.timer3played = false;
      this.timer4played = false;
      this.timer5played = false;
      //this.sounds.secondHalfPlayed = false;
    },
    handleTimer() {
      if (!this.timerRunning) {
        return;
      }

      const now = new Date();

      this.timeToShoot = Math.round(this.shootTime * 1000 + this.startTime.getTime() - now.getTime()) / 1000;
      this.timeToShoot = Math.round(this.timeToShoot * 100) / 100;
      this.mstimeToShoot = Math.round((this.shootTime * 1000 + this.startTime.getTime() - now.getTime()) / 10) % 100;

      if (this.timeToShoot < 5.0 && !this.timer5played) {
        this.sounds.timer5.play();
        this.timer5played = true;
      }

      if (this.timeToShoot < 4.0 && !this.timer4played) {
        this.sounds.timer4.play();
        this.timer4played = true;
      }

      if (this.timeToShoot < 3.0 && !this.timer3played) {
        this.sounds.timer3.play();
        this.timer3played = true;
      }

      if (this.timeToShoot < 2.0 && !this.timer2played) {
        this.sounds.timer2.play();
        this.timer2played = true;
      }

      if (this.timeToShoot < 1.0 && !this.timer1played) {
        this.sounds.timer1.play();
        this.timer1played = true;
      }

      if (this.timeToShoot < 0) {
        this.timeToShoot = 0;
        this.timerRunning = false;
        this.sounds.foul.play();
      }

    },
    onButtonClick() {
      if (!this.matchStarted) {
        this.start();
      } else {
        this.resetTimerSounds();
      }

      if (this.timerRunning) {
          this.sounds.beep.load();
        this.timerRunning = false;
      } else {

        this.sounds.beep.play();


        this.startTime = new Date();
        this.timeToShoot = this.shootTime;
        this.timerRunning = true;
      }
    },
    reset() {
      this.resetTimerSounds();

      this.secondHalfPlayed = false;
      this.twoMinutesPlayed = false;
      this.oneMinutePlayed = false;
      this.tensecondsPlayed = false;
      this.matchOverPlayed = false;

      this.matchStarted = false;
      this.timerRunning = false;
      this.timeToShoot = this.firstHalf
      this.mstimeToShoot = 100;
      this.matchTimeRemaining = this.matchDuration + ':00';
      this.mounted = true;
      this.switchedTimer = false;

      clearInterval(this.shotInterval);
      clearInterval(this.matchIntervall);
    },
  },

  beforeUnmount() {
    this.reset();
  },

  mounted() {
    this.sounds.timer5 = new Audio('/assets/sounds/5.mp3');
    this.sounds.timer4 = new Audio('/assets/sounds/4.mp3');
    this.sounds.timer3 = new Audio('/assets/sounds/3.mp3');
    this.sounds.timer2 = new Audio('/assets/sounds/2.mp3');
    this.sounds.timer1 = new Audio('/assets/sounds/1.mp3');
    this.sounds.foul = new Audio('/assets/sounds/foul.mp3');
    this.sounds.beep = new Audio('/assets/sounds/beep.mp3');
    this.sounds.secondHalf = new Audio('/assets/sounds/secondhalfshottimer.mp3');
    this.sounds.twoMinutes = new Audio('/assets/sounds/2minutesleft.mp3');
    this.sounds.oneMinute = new Audio('/assets/sounds/1minuteleft.mp3');
    this.sounds.tenseconds = new Audio('/assets/sounds/10seconds.mp3');
    this.sounds.matchOver = new Audio('/assets/sounds/matchover.mp3');
    this.sounds.timer1.addEventListener('canplaythrough', this.reset(), false);
  }
}
</script>

<style scoped lang="scss">
.layout {
  height: 100vh;
  display: grid;

  grid-template-rows: 50px auto 50px 200px;
}

.menu {
  width: 100%;
  background: #062d00;
}

.canvas {
  overflow: hidden;
}


.sbtn {
  display: block;
  width: 100%;
  height: 100%;
  background: #96ff86;
  font-size: 1rem;
  border: none;
}

.match-clock {
  background: #062d00;
  color: #f5a700;
  text-align: center;
  padding: 0.25rem;
  font-size: 2rem;
}
</style>
